.cm-message .cm-message-content {
    display: inline-block;
    position: relative;
    color: white;
    text-align: center;
    font-weight: bold;
    padding: 10px 25px;
    /*box-sizing: border-box;*/
}
.cm-message .cm-message-content .cm-message-close-btn {
      margin-left: 20px;
      cursor: pointer;
      border-radius: 20px;
      border: 2px solid white;
}
.cm-message .cm-message-content .cm-message-content-detail {
      position: relative;
}
.cm-message .cm-message-content .cm-message-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
}
.cm-message .cm-message-bg--info {
    background-color: #ababab;
}
.cm-message .cm-message-bg--error {
    background-color: #ff623b;
}
.cm-message .cm-message-bg--warning {
    background-color: #e2ce6c;
}
.cm-message .cm-message-bg--success {
    background-color: #3adfc5;
}
